.search-container{
    z-index: 1;
    position: sticky;;
    bottom: 0;
    .get-recommendations {
        background: linear-gradient(180deg, #87cdfa 0%, #5c9de5 80%, #5c9de5 100%);
        border: 0;
        letter-spacing: 1px;
        text-transform: none;
        height: 46px;
        font-size: 16px;
        font-weight: bold;
        cursor: pointer;
        a{
            cursor: pointer;
            color: white;
        }
    }
}