.home-container{
    .section1{
        background-color: #e8f3fa;
    }
   
    .big-title{
        background: none;
        font-weight: normal;
        letter-spacing: 2px;
    }
    .small-title{
        font-weight: normal;
        font-family: quicksand;
        margin-left: 0px;
        font-size: 20px;
        line-height: 28px;
        color: #666;
        margin-top: 16px;
    }
    .mob-banner{
        margin-top: 20px;
        margin-bottom: 20px;
        width: 100%;
    }
    .section-title {
        font-size: 21px;
        font-weight: bold;
        color: #000;
    }
    .section2{
        font-family: Quicksand;
        font-size: 17px;
    }
    .section3{
        .trust-img{
            width: 100%;
        }
        .epigram-img{
            width: 100%;
        }
    }
    .section4{
        .cfilters-img{
            width: 100%;
        }
    }

}
@media (max-width: 768px){
    .big-title {
        font-size: 30px;
        line-height: 34px;
    }
    .small-title {
        font-size: 16px;
        line-height: 22px;
        color: #666;
        margin-top: 16px;
    }
}
.veristeps{
    list-style: none;
    padding-left: 15px;
}
.veristeps li {
    border-left: 2px solid #b6dffb;
    line-height: 20px;
    font-size: 16px;
    padding: 0 1.5rem 2rem;
    position: relative;
    text-overflow: ellipsis;
    width: 100%;
}
.veristeps li:before {
    background: #fff;
    border: 2px solid #0fafff;
    border-radius: 100%;
    content: "";
    height: 20px;
    left: 0;
    margin-left: -10.5px;
    position: absolute;
    top: 0;
    width: 20px;
}