.search-results-container{
    .result-info{
        font-weight: normal;
        font-family: quicksand;
        margin-left: 0px;
        font-size: 20px;
        line-height: 28px;
        color: #666;
    }
    .results-list{
        .result-background{
            .result-image{
                width: 100%;
                opacity: 0.5;
            }
            .text-container{
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                font-size: 20px;
                .key-words{
                    background: #dcdbdb;
                    display: block;
                    text-align: center;
                    border-radius: 12px;
                    padding: 1px 5px;
                    margin-bottom: 10px;
                }
            }
            .text-container .key-words:nth-child(1){font-size: 28px;}
            .text-container .key-words:nth-child(2){font-size: 24px; position: relative;
                right: 30%;}
            .text-container .key-words:nth-child(3){font-size: 20px; position: relative;
                left: 30%;}
            .text-container .key-words:nth-child(4){font-size: 16px; position: relative;
                right: 30%;}
        }
    }
    
}

.img-animation-container{
    position: relative;
    .main-image{
        position: absolute;
        width: 50%;
        height: 50%;
    }
    .top-out-image{
        position: absolute;
    }
    .bottom-out-image{
        position: absolute;
    }
    .left-out-image{
        position: absolute;
    }
    .right-out-image{
        position: absolute;
    }
}